import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, ModalController, LoadingController, ToastController, ActionSheetController, AlertController } from 'ionic-angular';

import { IonicStorageProvider } from '../../providers/base-settings/storage';
import { CandidateProvider } from '../../providers/candidate/candidate';

@IonicPage()
@Component({
  selector: 'page-election-candidates',
  templateUrl: 'election-candidates.html'
})
export class ElectionCandidatesPage {
  public candidates: any;
  public default_image: string;
  public query: string;
  public next_offset: string;
  private sessionUser: any;
  private sessionToken: string = '';
  private searchName: string;
  public searchFilterBasic: any;
  private isSearched: boolean = false;
  private selected_lead = 0;

  /**
   * Constructor
   */
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public menuCtrl: MenuController, public modalCtrl: ModalController,
    private loadingCtrl: LoadingController, private toastCtrl: ToastController,
    private ionStorage: IonicStorageProvider, private actionSheetCtrl: ActionSheetController,
    private alertCtrl: AlertController, private candidateProv: CandidateProvider) {
    let env = this;

    // Initialize Targeted Lists Arr
    env.candidates = [];

    // Set Defaults
    env.default_image = 'assets/elections/elections.png';
    env.query = "";

    // Check User Session
    env.checkUserSession();

    // Temporary Data Collector
    env.getElectionCandidates();
  }

  /**
   * When control enters Ionic View
   */
  ionViewDidEnter() {
    let env = this;
    // Enabling the menu
    env.menuCtrl.enable(true, 'mainMenu');
  }

  /**
   * Check User Session
   */
  checkUserSession() {
    let env = this;

    this.ionStorage.getItems('sessionuser').then(data => {
      if(data && data.username) {
        env.sessionUser = data;
      } else {
        this.logout();
      }
    });
  }

  /**
   * Get Elction Details
   * This is a temporary method to fetch and show data
   */
  getElectionCandidates() {
    let env = this;
    env.candidates = env.candidateProv.getCandidates();
  }

  /**
   * Go to Candidates Listing for this election
   */
  gotoCandidatesListing() {
    this.navCtrl.push('ElectionCandidatesPage');
  }

  /**
   * Show Vote actions
   */
  showVoteActions(candidate) {
    const actionSheet = this.actionSheetCtrl.create({
      title: "Click the vote now button to vote for : "+ candidate.name,
      buttons: [
        {
          text: 'Vote Now',
          role: 'destructive',
          handler: () => {
            let loading = this.loadingCtrl.create({
              content: 'Please wait while we register your vote...'
            });

            loading.present();

            let params = {
              amount: 1,
              sender: this.sessionUser.name +"-"+ this.sessionUser.username,
              recipient: candidate.name
            };

            console.log(params);
            this.candidateProv.addVote(params).subscribe((data) => {
              console.log(data);
              loading.dismiss();

              let alert = this.alertCtrl.create({
                title: 'Success',
                subTitle: 'Successfully voted.',
                buttons: ['Done']
              });

            }, (err) => {
              loading.dismiss();

              let alert = this.alertCtrl.create({
                title: 'Failure',
                subTitle: 'Unable to cast a vote at the moment. Please try again later.',
                buttons: ['Dismiss']
              });
              alert.present();
            });
         }
        },{
          text: 'Cancel',
          role: 'cancel',
        }
      ]
    });
    actionSheet.present();
  }

  /**
   * Logout
   */
  logout() {
    this.ionStorage.removeItem('sessionuser');
    this.navCtrl.setRoot('LoginPage');
  }
}



